<template>
  <v-container fill-height>
    <v-form class="login text-center" lazy-validation ref="form" v-model="valid">
      <v-text-field placeholder="email" v-model="email" :rules="emailRules" />
      <v-text-field placeholder="password" type="password" v-model="password" />
      <v-btn color="primary" :disabled="!!loadingMsg || !valid" @click="login">登陆</v-btn>
    </v-form>
  </v-container>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Login',
  computed: { ...mapState(['loadingMsg', 'error']) },

  data() {
    return {
      valid: false,
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        // v => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
      password: '',
      passwordRules: [
        v => !!v || 'Password is required'
      ]
    };
  },

  methods: {
    async login() {
      const { email, password } = this;
      await this.$store.dispatch('login', { email, password });
      if (!this.error) {
        await this.$router.replace('/');
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.login {
  width: 100%;
  text-align: center;

  >* {
    max-width: 400px;
    min-width: 150px!important;
    margin-left: auto;
    margin-right: auto;
  }
}
</style>