import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

import user from './user';

export default new Vuex.Store({
  state: {
    loadingMsg: null,
    requests: null,
    error: null
  },
  mutations: {
    setRequests(store, requests) {
      store.requests = requests;
    },

    setLoading(store, msg) {
      store.loadingMsg = msg;
      store.error = null;
      if (!msg) {
        store.requests = null;
      }
    },

    setError(store, e) {
      store.error = e;
      store.loadingMsg = null;
      if (!e) {
        store.requests = null;
      }
    }
  },
  actions: {
    async request({ commit, state }, { requests, request, retry = true, show = true }) {
      requests = requests || [request];
      if (state.requests) {
        window.console.error('Requests is processing.');
        return ;
      }

      if (retry && show) commit('setRequests', requests);

      for (const [req, msg] of requests) {
        const promise = req();
        if (show) commit('setLoading', msg);
        try {
          await promise;
        } catch (e) {
          if (show) commit('setError', e);
          return ;
        }
      }

      commit('setLoading');
    },

    async retry({ dispatch, commit, state }) {
      const requests = state.requests;
      commit('setError', null);
      await dispatch('request', requests);
    },

    clearError({ commit }) {
      commit('setError', null);
    }
  },
  modules: {
    user
  }
})
