<template>
  <div :class="{ battery: true, offline: !isOnline }" :style="{ height: (height || 40) + 'px' }">
    <div class="left text-center" :style="{ 'line-height': (height || 40) + 'px' }">
      <div class="percent" :style="{ right: (100 - battery_level) + '%' }"></div>
      <span>
        {{battery_range | km}}/{{totalRange | km}} {{battery_level}}%
      </span>
    </div>
    <div class="right top"></div>
    <div class="right middle"></div>
    <div class="right bottom"></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'Battery',
  props: ['height'],
  computed: {
    ...mapGetters(['isOnline', 'vehicleData']),
    battery_range() {
      return this.vehicleData.charge_state.battery_range;
    },
    battery_level() {
      return this.vehicleData.charge_state.battery_level;
    },
    totalRange() {
      return this.battery_range / this.battery_level * 100;
    }
  }
}
</script>

