import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import vuetify from './plugins/vuetify';

Vue.config.productionTip = false;

Vue.filter('km', (v, d = 0) => typeof v === 'number' && (v * 1.609344).toFixed(d) + 'km');
Vue.filter('mile', (v, d = 0) => typeof v === 'number' && v.toFixed(d) + 'mi');
Vue.filter('hour', v => {
  if (v) {
    const hour = Math.floor(v);
    const min = Math.floor((v - hour) * 60);
    return (hour && hour + '小时') + (min > 0 ? (min + '分钟') : '');
  }
});

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app');
