<template>
  <v-container>
    <v-row v-if="vehicleData">
      <v-col class="text-center" @click="getVehicleState">
        <b>{{ vehicleData.display_name }}</b>
        &nbsp;<v-icon x-small>mdi-refresh</v-icon>
      </v-col>
    </v-row>
    <v-row v-if="vehicleData.vehicle_state">
      <v-col cols="7">
        <charging v-if="state === 'Charging'" />
        <driving v-else-if="!!state" />
        <parked v-else />
      </v-col>
      <v-col cols="5">
        <battery />
      </v-col>
    </v-row>
    <br/>
    <v-divider />
    <div class="text-center" v-if="vehicleData.vehicle_state">
      <br/>
      {{model}}<br/>
      {{vehicleData.vehicle_state.odometer | km}}<br/>
      版本：{{vehicleData.vehicle_state.car_version}}
    </div>
<!--    {{ vehicleData }}-->
  </v-container>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';

import Battery from "@/components/Battery";
import Charging from "@/components/Charging";
import Driving from "@/components/Driving";
import Parked from "@/components/Parked";

const model = {
  'MDLS': 'Model S',
  'MDL3': 'Model 3',
  'MDLX': 'Model X',
  'MDLY': 'Model Y'
};

export default {
  name: 'Home',
  components: { Charging, Driving, Parked, Battery },
  comments: { Battery },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['vehicleData']),
    state() {
      const { charging_state } = this.vehicleData.charge_state;
      if (['Charging', 'Complete'].indexOf(charging_state) >= 0) return 'Charging';
      return this.vehicleData.drive_state.shift_state;
    },
    model() {
      const opc = this.vehicleData.option_codes && this.vehicleData.option_codes.split(',');
      for (const c of opc) {
        if (model[c]) return model[c];
      }
      return '';
    }
  },
  methods: { ...mapActions(['listVehicles', 'getVehicleState']) },

  async beforeMount() {
    if (this.user.user) {
      await this.listVehicles();
    }
  }
}
</script>
