<template>
  <v-app>
    <v-main>
      <router-view />

      <v-card light class="bottom" v-if="loadingMsg || error" :loading="!!loadingMsg">
        <v-card-title class="text-center">{{loadingMsg || error && '错误'}}</v-card-title>
        <v-card-text class="text-center">
          {{error}}
<!--          <v-btn v-if="requests" color="primary" @click="retry">重试</v-btn>-->
          <br/>
          <v-btn @click="clearError">{{ error ? '关闭' : '取消'}}</v-btn>
        </v-card-text>
      </v-card>
    </v-main>
  </v-app>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'App',
  computed: { ...mapState(['user', 'loadingMsg', 'error', 'requests']) },
  methods: { ...mapActions(['retry', 'clearError']) },

  beforeMount() {
    this.$vuetify.theme.dark = true;
    if (!this.user.user) this.$router.replace('/login');
  }
};
</script>

<style lang="scss" scoped>
.bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}
</style>