import Vue from 'vue';

require('async-tesla-api/src/request').urlBase = 'https://api.i43.io/tesla';
import { oauth, User } from 'async-tesla-api';

const userStoreKey = 'user';
const currentVehicleVinStoreKey = 'vehicle';

const userStore = JSON.parse(window.localStorage.getItem(userStoreKey) || 'null');
const currentVehicleVin = window.localStorage.getItem(currentVehicleVinStoreKey);
const vehicleData = JSON.parse(window.localStorage.getItem(currentVehicleVin) || '{}');
delete vehicleData.state;

export default {
  state: {
    user: userStore && Date.now() < (userStore.created_at + userStore.expires_in + 3600) * 1000 && new User(userStore),
    vehicles: [],
    currentVehicleVin,
    lastUpdate: 0,
    vehicleData
  },
  getters: {
    vehicle(state) {
      return state.vehicles.find(v => v.data.vin === state.currentVehicleVin);
    },

    vehicleData(state) {
      return state.vehicleData;
    },

    isOnline(state) {
      return state.lastUpdate + 60 * 1000 > Date.now() && state.vehicleData.state === 'online';
    }
  },
  mutations: {
    setVehicleData(state, vd) {
      Vue.set(state, 'vehicleData', vd);
      window.localStorage.setItem(vd.vin, JSON.stringify(vd));
      state.lastUpdate = Date.now();
    }
  },
  actions: {
    async login({ dispatch, state }, { email, password }) {
      await dispatch('request', {
        request: [
          async () => {
            const user = await oauth.login(email, password);
            window.localStorage.setItem(userStoreKey, JSON.stringify(user.token));
            state.user = new User(user.token);
          },
          '登陆中'
        ],
        retry: false
      });
    },

    async getVehicleState({ dispatch, commit, getters: { vehicle } }) {
      await dispatch('request', {
        request: [
          async () => {
            try {
              commit('setVehicleData', await vehicle.getStatus());
            } catch (e) {
              setTimeout(async () => {
                await dispatch('request', {
                  requests: [
                    [() => vehicle.wakeUp(), '唤醒中'],
                    [
                      async () => {
                        await new Promise(resolve => setTimeout(resolve, 5000));
                        commit('setVehicleData', await vehicle.getStatus());
                      },
                      '获取车辆信息'
                    ]
                  ]
                });
              }, 0);
            }
          },
          '获取车辆信息'
        ],
        retry: false
      });
    },

    async listVehicles({ dispatch, state }) {
      await dispatch('request', {
        request: [
          async () =>  {
            const vehicles = state.vehicles = await state.user.listVehicles();
            state.lastUpdate = Date.now();

            let currentVehicle = vehicles.find(v => v.data.vin === state.currentVehicleVin);
            if (!currentVehicle && state.currentVehicleVin) {
              window.localStorage.removeItem(currentVehicleVinStoreKey);
              window.localStorage.removeItem(state.currentVehicleVin);
              state.currentVehicleVin = null;
              state.vehicleData = {};
            }

            if (!state.currentVehicleVin && vehicles.length > 0) {
              currentVehicle = vehicles[0];
              window.localStorage.setItem(
                currentVehicleVinStoreKey,
                state.currentVehicleVin = vehicles[0].data.vin
              );
              Vue.set(state, 'vehicleData', vehicles[0].data);
            }

            if (currentVehicle && currentVehicle.data.state === 'online') {
              setTimeout(() => dispatch('getVehicleState'), 0);
            }
          },
          '获取车辆列表'
        ],
        show: state.vehicles.length === 0
      });
    }
  }
}