<template>
  <v-card>
    <v-card-title class="title">
      <span :class="{ active: state.shift_state === 'P' }">P</span>
      <span :class="{ active: state.shift_state === 'R' }">R</span>
      <span :class="{ active: state.shift_state === 'N' }">N</span>
      <span :class="{ active: state.shift_state === 'D' }">D</span>
    </v-card-title>
    <v-card-text>
      <h1 class="speed">{{state.speed || 0 | km}}/h</h1>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'Driving',
  computed: {
    ...mapGetters(['vehicleData']),
    state() {
      return this.vehicleData.drive_state;
    }
  }
}
</script>

<style scoped lang="scss">
.title {
  span {
    padding: 0 5px;
    color: #555;

    &.active {
      color: white;
    }
  }
}

.speed {
  color: white;
}
</style>