<template>
  <v-card>
    <v-card-title>P {{chargingState}}</v-card-title>
    <v-card-text>
      <v-row dense>
        <v-col cols="2"><v-icon small>mdi-lightning-bolt</v-icon></v-col>
        <v-col cols="10">
          {{state.charge_rate|km}}/h | {{state.charger_power}}kw<br/>
          +{{state.charge_miles_added_ideal|km}} | +{{state.charge_energy_added}}kwh
        </v-col>
      </v-row>
      <v-row dense v-if="state.time_to_full_charge > 0">
        <v-col cols="2"><v-icon small>mdi-clock</v-icon></v-col>
        <v-col cols="10">{{state.time_to_full_charge|hour}}</v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'Charging',
  computed: {
    ...mapGetters(['vehicleData']),
    state() {
      return this.vehicleData.charge_state;
    },
    chargingState() {
      const { charging_state } = this.state;
      if (charging_state === 'Charging') return '充电中';
      if (charging_state === 'Complete') return '充电完成';
      return charging_state + '';
    }
  }
}
</script>

